import React, {FC} from "react";
import style from "./Header2.module.scss";
import logo from "../../assets/png/header2/logo.png";

import clsx from "clsx";
import {Link} from "react-router-dom";
import {buttons, links} from "./constants";
import {SvgIcon} from "../../components/common/SvgIcon/SvgIcon";
import {svgIcons} from "../../assets/svg/svgIcons";


export interface IHeader2 {
    burgerMenuIsOpen: boolean
    onClickHandler: (open: boolean) => void
    // dir: number
    // headerIsTransparent: boolean
}

export const Header2: FC<IHeader2> = ({
                                          burgerMenuIsOpen,
                                          onClickHandler,
                                          // dir,
                                          // headerIsTransparent
                                      }) => {
    return (
        <header className={style.header2}>

            <div className={clsx({
                [style.inner]: true,
                //[style.inner_hide]: dir === 1 && !headerIsTransparent,
                //[style.inner_transparent]: headerIsTransparent,
            })}>
                <div className={style.left}>
                    <Link to="/app2" className={style.logo}>
                        <img src={logo} alt=""/>
                    </Link>
                    <nav className={style.links}>
                        {
                            links.map(({label, to}, index) => (
                                    <Link className={style.link}
                                          key={index}
                                          to={to}
                                    >
                                        {label}
                                    </Link>
                                )
                            )
                        }
                    </nav>
                </div>

                <div className={style.right}>
                    <div className={style.buttons}>
                        {
                            buttons.map(({src, text, label}, index) => (
                                <div key={index}
                                     className={style.btnBlock}
                                >
                                    <img src={src} alt=""/>
                                    <div className={style.right}>
                                        <p>{text}</p>
                                        <button>{label}</button>
                                    </div>
                                </div>
                            ))
                        }
                    </div>

                    <button className={style.burgerButton}
                            onClick={() => onClickHandler(!burgerMenuIsOpen)}
                    >
                        <SvgIcon icon={
                            burgerMenuIsOpen ? svgIcons.close : svgIcons.burger
                        }/>
                    </button>

                </div>




            </div>

        </header>
    )
}