import * as React from "react";
import style from "./HomePage.module.scss";
import {links} from "./constants";
import { Link } from "react-router-dom";
import imgD from "../../assets/png/home2/btn_D_Desktop.png";
import imgH from "../../assets/png/home2/btn_H_Desktop.png";
import imgM from "../../assets/png/home2/btn_D_Mobile.png";
import {HashLink} from "react-router-hash-link";


export const HomePage = () => {
    return (
        <div className={style.homePage}>
            <div className={style.inner}>
                <h1>First Tournament starting in:</h1>
                <p className={style.timer}>3D:24H:24M</p>

                <div className={style.links}>
                    {
                        links.map(({label, to, src}, index) => (
                            <HashLink key={index}
                                  to={to}
                                  className={style.link}
                            >
                                <img src={imgD} alt="" className={style.imgD}/>
                                <img src={imgH} alt="" className={style.imgH}/>
                                <img src={imgM} alt="" className={style.imgMobile}/>

                                <img src={src} alt="" className={style.icon}/>
                                <p className={style.label}>{label}</p>
                            </HashLink>
                        ))
                    }
                </div>


            </div>
        </div>
    )
}