import * as React from "react";
import style from "./App2.module.scss"
import {Route, Routes} from "react-router-dom";
import {HomePage} from "../B0_HomePage/HomePage";
import {TournamentsPage} from "../B1_TournamentsPage/TournamentsPage";
import {Header2} from "../A1_Header2/Header2";
import {useState} from "react";
import {Footer2} from "../A2_Footer2/Footer2";

export const App2 = () => {
    const [burgerMenuIsOpen, setBurgerMenuIsOpen] = useState(false);


    return (
        <div className={style.app2}>
            <Header2 burgerMenuIsOpen={burgerMenuIsOpen}
                     onClickHandler={(open: boolean) => setBurgerMenuIsOpen(open)}
            />
            <Routes>
                <Route path="/" element={<HomePage/>}/>
                <Route path="/tournaments" element={<TournamentsPage/>}/>
            </Routes>
            <Footer2/>
        </div>
    )
}